<script>
import { yandexMap } from 'vue-yandex-maps'
import ButtonAdd from './ui/ButtonAdd'
import ToggleButtonGroup from '@/components/ui/ToggleButtonGroup'
import MaskedInput from 'vue-masked-input'
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import "swiper/css/swiper.css"

export default {
  name: 'CommonData',
  components: {
    ButtonAdd,
    Swiper,
    SwiperSlide,
    MaskedInput,
    yandexMap,
    ToggleButtonGroup
  },
  data() {
    return {
      selected: " (Activated)",
      tabs: {
        ' (Activated)': ' (Activated)',
        ' (Block)': ' (Block)'
      },
      OpenIndicator: {
        render: createElement => createElement('span', {class: {'toggle-arrow': true}}),
      },
      swiperOption: {
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev',
        },
        spaceBetween: 70,
        pagination: {
          el: '.swiper-pagination',
          type: 'custom',
          renderCustom: function (swiper, current, total) {
            return `<div>${current}  ${total}</div>`
          }
        }
      },
      selectStatus: '',
      selectType: '',
      selectDistributor: '',
      selectChannel: '',
      selectSubchannel: '',
      selectCountry: '',
      selectRegion: '',
      selectCity: '',
      selectReasons: '',
      selectResponsible: '',
      selectResponsibleDistributor: '',
      selectSchedule: [],
      selectSchedule2: [],
      optionsSchedule: [
        { dayLong: '', dayShort: '' },
        { dayLong: '', dayShort: '' },
        { dayLong: '', dayShort: '' },
        { dayLong: '', dayShort: '' },
        { dayLong: '', dayShort: '' },
        { dayLong: '', dayShort: '' },
        { dayLong: '', dayShort: '' }
      ],
      dateStartWork: null,
      dateStartAgreement: null,
      dateFinishedAgreement: null,
      inputWebsite: null,
      inputEmail: null,
      inputTime: null,
      daysWeek: null
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.$refs.mySwiper.updateSwiper()
    });
  },
  methods: {
    changeSelectVal(val) {
      this.selected = val
    }
  }
}
</script>
